import React, { useEffect, useState } from 'react';
import { Button } from 'antd';
import bannergif from '../assets/bannergif.gif';
import bannergifMobile from '../assets/bannergifMobile.png';
import randomgif from '../assets/randomgif.gif';
import { notify } from '../utils/notifications';
import { sendToMinter } from '../utils/send';
import { useConnection } from '../utils/connection';
import { useWallet } from '../utils/wallet';
import axios from 'axios';
export var urlMarket = "";

export default function TradePage() {
  return (
    <TradePageInner />
  );
}

function TradePageInner() {
  const connection = useConnection();
  const {wallet, connected} = useWallet();
  const [dimensions, setDimensions] = useState({
    height: window.innerHeight,
    width: window.innerWidth,
  });
  const [remaining, setRemaining] = useState<string>("xxxxx");

  useEffect(() => {
    axios.get('https://4ebst61dg0.execute-api.us-east-2.amazonaws.com/')
    .then(response => {
      var result = JSON.stringify(response["data"]);
      setRemaining(result);
      // console.log("remainging: " + result);
    });
  }, []);

  useEffect(() => {
    const handleResize = () => {
      setDimensions({
        height: window.innerHeight,
        width: window.innerWidth,
      });
    };

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  const width = dimensions?.width;
  // const componentProps = {
  //   onChangeOrderRef: (ref) => (changeOrderRef.current = ref),
  //   onPrice: useCallback(
  //     (price) => changeOrderRef.current && changeOrderRef.current({ price }),
  //     [],
  //   ),
  //   onSize: useCallback(
  //     (size) => changeOrderRef.current && changeOrderRef.current({ size }),
  //     [],
  //   ),
  // };
  const component = (() => {
    if (width < 1000) {
      return <RenderSmaller {...{connection, connected, wallet, remaining }} />;
    } else if (width < 1450) {
      return <RenderSmall {...{connection, connected, wallet, remaining }} />;
    } else {
      return <RenderNormal {...{connection, connected, wallet, remaining }} />;
    }
  })();

  return (
    <>
      {component}
    </>
  );
}

const RenderSmaller = ({ connection, connected, wallet, remaining  }) => {
  return (
    <>
      <div style={{display: 'flex', justifyContent: 'center', paddingBottom:'2rem'}}>
        <img src={bannergifMobile} width="90%" height="65px"/>
      </div>
      <div style={{display: 'flex', justifyContent: 'center', paddingBottom:'2rem'}}>
          <Button size={"large"} disabled={true} onClick={() => {
            if (connected && wallet) {
              sendToMinter({connection, wallet});
            } else {
              notify({
                message: 'Please connect your wallet to mint',
                type: 'error',
              });
            }
          }}>
            {'Disabled: Mint Solana Doge'}
            {/* {'Mint Solana Doge (2.5 SOL)'} */}
          </Button>
      </div>
      <div style={{ display: 'flex', justifyContent: 'center', paddingTop: '0.1rem', width: '100%' }}>
          <h1 style={{ color: '#3ebfbf', textAlign: 'center', width: '90%', fontWeight: 'bold' }}>
            Remaining: 5000/5000
            {/* Remaining: {remaining}/5000 */}
          </h1>
      </div>
      <div style={{ display: 'flex', justifyContent: 'center', paddingTop: '1rem', width: '100%' }}>
          <h2 style={{ color: '#bc13fe', textAlign: 'center', paddingTop: '0.5rem', width: '90%' }}>
              SolanaDogeNFTs is a collection of the cutest, cleanest, classiest, coolest NFTs around.
              Secured by the Solana network, each Solana Doge combines multiple layers
              of potential traits to form a unique combination. No two Solana Doge NFTs are the same!
              There is a cap of 5,000 to be minted. Simply connect your wallet and mint for 2.5 SOL.

          </h2>
      </div>
      <div style={{ display: 'flex', justifyContent: 'center', paddingTop: '0.5rem', width: '100%' }}>
          <h2 style={{ color: '#bc13fe', textAlign: 'center', paddingTop: '1rem', width: '90%' }}>
            Collect, match, and discover! 
          </h2>
      </div>
      <div style={{display: 'flex', justifyContent: 'center', paddingBottom:'2rem'}}>
        <img src={randomgif} width="100%" height="200px"/>
      </div>
    </>
  );
};

